import React, {useEffect, useState} from "react";
import blobHelper from "../../components/helpers/Blob";
import {Swiper, SwiperSlide} from 'swiper/react';
import 'swiper/components/autoplay';
import 'swiper/swiper-bundle.css';

import SwiperCore, {
    Autoplay, Pagination, Navigation
} from 'swiper';
import {graphql, useStaticQuery} from "gatsby";
import Img from "gatsby-image";
import {useTranslation} from "react-i18next";

SwiperCore.use([Autoplay, Pagination, Navigation]);

export function Clients() {
    const {t} = useTranslation();
    const [setSwiperRef] = useState(null);


    useEffect(() => {

        blobHelper.createBlob({
            element: document.querySelectorAll(".outerCircle"),
            numPoints: 10,
            centerX: (100 / 2),
            centerY: (100 / 2),
            minRadius: 45,
            maxRadius: 45 + 3,
            minDuration: 1,
            maxDuration: 3
        })

        blobHelper.createBlob({
            element: document.querySelectorAll(".outerTriangle"),
            numPoints: 3,
            centerX: (100 / 2),
            centerY: (100 / 2),
            minRadius: (42) + 5,
            maxRadius: (42 + 3) + 5,
            minDuration: 1,
            maxDuration: 3
        })
    }, [])
    const data = useStaticQuery(graphql`
    query {
      allWpPost(
        sort: {fields: [date], order: DESC}
        filter: {categories: {nodes: {elemMatch: {name: {eq: "clients"}}}}}

      ) {
          nodes {
            id
            title
            excerpt
            language {
              slug
            }
            featuredImage {
              node {
                sourceUrl
                 localFile {
                   publicURL
                   childImageSharp {
                     gatsbyImageData(
                        placeholder: BLURRED
                     )
                     fixed(width: 100, quality: 100) {
                         ...GatsbyImageSharpFixed
                     }
                   }
                 }
              }
            }
          }
      }
    }
  `)
    return (
        <section className="mt-20">
            <div>
                <div className="container mx-auto px-4 pb-14">
                    <div className="text-center text-2xl md:text-5xl glitch" data-glitch={t('trusted_by')}>
                        {t('trusted_by')}
                    </div>
                </div>
                <Swiper spaceBetween={30} slidesPerView={3} onSwiper={setSwiperRef}
                        speed={2500}
                        autoplay={{
                            "delay": 2500,
                            "disableOnInteraction": false
                        }}
                        breakpoints={{
                            0: {
                                slidesPerView: 1,
                            },
                            768: {
                                slidesPerView: 3,
                            },
                        }}
                        loop={true}
                        className="mySwiper">
                    {data.allWpPost.nodes.map((node, index) => (
                        <SwiperSlide key={index} className="swiper-slide">
                            <div className="bg-contain bg-no-repeat bg-center text-center h-60 relative">
                                <svg viewBox="0 0 100 100" width={'100%'} height={'100%'} className="css--partner-svg">
                                    <path stroke='#fff' fill="transparent" className={(index % 2 ? "outerCircle" : "outerTriangle")}/>
                                </svg>
                                <a href={node.excerpt.replace( /(<([^>]+)>)/ig, '')}>
                                    <Img className="css--partners-logo" objectFit="contain" fixed={node.featuredImage.node.localFile.childImageSharp.fixed} alt="Client image"/>
                                </a>
                            </div>
                        </SwiperSlide>
                    ))}
                </Swiper>

            </div>
        </section>


    )
}
