import React, {lazy, useLayoutEffect} from "react"
import LoadingScreen from "../components/elements/LoadingScreen";
import {Clients} from "../layout/Partnership/Clients";
import FadedText from "../components/helpers/FadedText";
import anime from "animejs";

const Navigation = lazy(() => import("../components/Navigation"));
const Partners = lazy(() => import("../layout/Partnership/Partners"));
const Feedbacks = lazy(() => import("../layout/Partnership/Feedbacks"));

const OurPartners = () => {

    useLayoutEffect(() => {
        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            targets: '#our-partners',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            scale: [0.8, 1],
            duration: 1000,
        });
        timeline.add({
            targets: '.css--fadedtext',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            translateX: ["-100%", 0],
            duration: 500
        });
    }, [])

    return (
        <div>
            <React.Suspense fallback={<LoadingScreen/>}>
                <Navigation pageNr={'03'}/>
                <FadedText text={'EXPAND'} direction="left" />

                <div id="our-partners" className="pt-14">
                    <Partners/>
                    <Feedbacks/>
                    <Clients/>
                </div>
            </React.Suspense>
        </div>

    )
}
export default OurPartners;


